import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import PrintNota from "../../Stocking/Component/PrintNota";
import HeaderCard from "../../HeaderCard";
import FormLaporanPenerimaanSupplierHead from "./FormLaporanPenerimaanSupplierHead";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";
import { formatGram, formatRp } from "../../helper";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
class FormLaporanPenerimaanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listNota: [],
      nama_supplier: "",
      columns: [
        {
          dataField: "tanggal_invoice",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "total_bruto",
          text: "Total Bruto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto",
          text: "Total Netto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto_ext",
          text: "Total Netto Ext",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_rp",
          text: "Ongkos(Rp)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.print(row)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      listPenerimaan: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  getDetails(faktur) {
    AxiosMasterGet(
      `report/terima-barang-supplier/DETAIL&${faktur.kode_supplier}&${faktur.tanggalAwal}&${faktur.tanggalAkhir}`
    ).then((res) =>
      this.setState({
        listNota: res.data[0].detail,
        nama_supplier: res.data[0].nama_supplier,
      })
    );
  }
  setLocal(data) {
    localStorage.setItem(
      "TerimaSupplierTable",
      JSON.stringify(data.barangDetails)
    );
    this.print(data.tanggal, data.noBon, data.noFaktur, data.kodeSupplier);
  }
  print(data) {
    console.log(data);
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = data.detail_barang;
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        { content: ++i },
        { content: ticket.kode_jenis },
        { content: ticket.qty, styles: { halign: "right" } },
        { content: ticket.kadar_cetak, styles: { halign: "right" } },
        { content: formatGram(ticket.plastik), styles: { halign: "right" } },
        { content: formatGram(ticket.kulit), styles: { halign: "right" } },
        { content: ticket.pkg, styles: { halign: "right" } },
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        { content: ticket.kadar_beli, styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        { content: formatRp(ticket.ongkos_rp), styles: { halign: "right" } },
        { content: formatRp(ticket.harga_beli), styles: { halign: "right" } },
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      {
        content: table
          .map((list) => parseFloat(list.qty))
          .reduce((a, b) => a + b, 0),
        styles: { halign: "right" },
      },
      "",
      "",
      "",
      {
        content: table
          .map((list) => parseFloat(list.pkg))
          .reduce((a, b) => a + b, 0),
        styles: { halign: "right" },
      },
      {
        content: formatGram(
          table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      "",
      {
        content: formatGram(
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0),
          3
        ),
        styles: { halign: "right" },
      },
      {
        content: formatRp(
          table
            .map((list) => parseInt(list.ongkos_rp))
            .reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      {
        content: formatRp(
          table
            .map((list) => parseInt(list.harga_beli))
            .reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
    ];
    // tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      [
        { content: "No" },
        { content: "Kode\nModel" },
        { content: "Qty", styles: { halign: "right" } },
        { content: "Cap\nKode", styles: { halign: "right" } },
        { content: "P", styles: { halign: "right" } },
        { content: "K", styles: { halign: "right" } },
        { content: "Bungkus\n(Gr)", styles: { halign: "right" } },
        { content: "Berat\n(Gr)", styles: { halign: "right" } },
        { content: "Harga\n(Gr)", styles: { halign: "right" } },
        { content: "Murni\n(Gr)", styles: { halign: "right" } },
        { content: "Ongkos\n(Rp)", styles: { halign: "right" } },
        { content: "Harga\nBeli(Rp)", styles: { halign: "right" } },
      ],
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "No Nota",
      data.no_bon,
      "Tanggal",
      data.tanggal_invoice,
      "",
      "",
      "Supplier",
      this.state.nama_supplier,
      "ADMIN",
      getToday(),
      "",
      columTable,
      "Terima Barang Supplier",
      tableRows,
      footer
    );
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  caridata(faktur) {
    AxiosMasterGet(
      `report/terima-barang-supplier/DETAIL&${faktur.kode_supplier}&${faktur.tanggalAwal}&${faktur.tanggalAkhir}`
    ).then((res) =>
      this.setState({
        listNota: res.data[0].detail,
      })
    );
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => console.log("BACK")}
              >
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Penerimaan Supplier" />
            <div className="card-body">
              <br />
              <div className="col-lg-12">
                <FormLaporanPenerimaanSupplierHead
                  onSubmit={(data) => this.getDetails(data)}
                />
              </div>
              <div className="col-lg-12">
                {this.state.listPenerimaan ? (
                  <ToolkitProvider
                    bootstrap4
                    keyField={this.state.listPenerimaan}
                    data={this.state.listNota}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="col-lg-12 col-md-12 mt-5">
                        <div className="text-right">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0 }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          headerClasses="header-table"
                          striped
                          hover
                          bordered={true}
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="NOT RESPOND"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <Skeleton width="100%" height={180} />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanPenerimaanSupplier);
